<template>
  <h2>1. Start your Webcam</h2>
  <div class="videos">
      <span>
        <h3>Local Stream</h3>
        <video id="webcamVideo" ref="webcamVideo" autoplay playsinline></video>
      </span>
    <span>
        <h3>Remote Stream</h3>
        <video id="remoteVideo" ref="remoteVideo" autoplay playsinline></video>
      </span>


  </div>

  <button id="webcamButton" @click="start">Start webcam</button>
  <h2>2. Create a new Call</h2>
  <button id="callButton" @click="create" :disabled="callButtonDisabled">Create Call (offer)</button>

  <h2>3. Join a Call</h2>
  <p>Answer the call from a different browser window or device</p>

  <input id="callInput" ref="callInput"/>
  <button id="answerButton" @click="join" :disabled="answerButtonDisabled">Answer</button>

  <h2>4. Hangup</h2>

  <button id="hangupButton" @click="hangup" :disabled="hangButtonDisabled">Hangup</button>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
// eslint-disable-next-line import/no-extraneous-dependencies
import firebase from 'firebase/app';
import 'firebase/firestore';

const firebaseConfig = {
  // your config
  projectId: "test-c203e"
};

const app = firebase.initializeApp(firebaseConfig);
const firestore = firebase.firestore();

const servers = {
  iceServers: [
    {
      urls: ['stun:stun1.l.google.com:19302', 'stun:stun2.l.google.com:19302'],
    },
  ],
  iceCandidatePoolSize: 10,
};

export default {
  name: 'HomeView',
  components: {
    HelloWorld
  },
  data: () => {
    return {
      localStream: null,
      remoteStream: null,
      callButtonDisabled: false,
      answerButtonDisabled: false,
      webcamButtonDisabled: false,
      hangButtonDisabled: false,
      pc: new RTCPeerConnection(servers),
    }
  },
  methods: {
    async start() {
      console.log("webcamButton")
      this.localStream = await navigator.mediaDevices.getUserMedia({video: true, audio: true});
      this.remoteStream = new MediaStream();

      // Push tracks from local stream to peer connection
      this.localStream.getTracks().forEach((track) => {
        this.pc.addTrack(track, this.localStream);
      });

      // Pull tracks from remote stream, add to video stream
      this.pc.ontrack = (event) => {
        event.streams[0].getTracks().forEach((track) => {
          this.remoteStream.addTrack(track);
        });
      };

      this.$refs.webcamVideo.srcObject = this.localStream;
      this.$refs.remoteVideo.srcObject = this.remoteStream;

      this.callButtonDisabled = false;
      this.answerButtonDisabled = false;
      this.webcamButtonDisabled = true;
    },
    async create() {
      // Reference Firestore collections for signaling
      // const callDoc = firestore.collection('calls').doc();
      // const offerCandidates = callDoc.collection('offerCandidates');
      // const answerCandidates = callDoc.collection('answerCandidates');

      const callDoc = firestore.collection('calls').doc();
      const offerCandidates = callDoc.collection('offerCandidates');
      const answerCandidates = callDoc.collection('answerCandidates');

      console.log("callDoc", callDoc)

      this.$refs.callInput.value = callDoc.id;

      // Get candidates for caller, save to db
      this.pc.onicecandidate = (event) => {
        event.candidate && offerCandidates.add(event.candidate.toJSON());
      };

      // Create offer
      const offerDescription = await this.pc.createOffer();
      await this.pc.setLocalDescription(offerDescription);

      const offer = {
        sdp: offerDescription.sdp,
        type: offerDescription.type,
      };

      await callDoc.set({offer});

      // Listen for remote answer
      callDoc.onSnapshot((snapshot) => {
        const data = snapshot.data();
        if (!this.pc.currentRemoteDescription && data?.answer) {
          const answerDescription = new RTCSessionDescription(data.answer);
          this.pc.setRemoteDescription(answerDescription);
        }
      });

      // When answered, add candidate to peer connection
      answerCandidates.onSnapshot((snapshot) => {
        snapshot.docChanges().forEach((change) => {
          if (change.type === 'added') {
            const candidate = new RTCIceCandidate(change.doc.data());
            this.pc.addIceCandidate(candidate);
          }
        });
      });

      this.hangButtonDisabled = false;
    },
    async join() {
      const callId = this.$refs.callInput.value;
      const callDoc = firestore.collection('calls').doc(callId);
      const answerCandidates = callDoc.collection('answerCandidates');
      const offerCandidates = callDoc.collection('offerCandidates');

      this.pc.onicecandidate = (event) => {
        event.candidate && answerCandidates.add(event.candidate.toJSON());
      };

      const callData = (await callDoc.get()).data();

      const offerDescription = callData.offer;
      await this.pc.setRemoteDescription(new RTCSessionDescription(offerDescription));

      const answerDescription = await this.pc.createAnswer();
      await this.pc.setLocalDescription(answerDescription);

      const answer = {
        type: answerDescription.type,
        sdp: answerDescription.sdp,
      };

      await callDoc.update({answer});

      offerCandidates.onSnapshot((snapshot) => {
        snapshot.docChanges().forEach((change) => {
          console.log(change);
          if (change.type === 'added') {
            let data = change.doc.data();
            this.pc.addIceCandidate(new RTCIceCandidate(data));
          }
        });
      });
    },
    async hangup(){
      //todo
    }
  }
}
</script>

